<template>
    <div class="box box-primary">
        <div class="box-header with-border">
            <h3 class="box-title">Thông báo</h3>
        </div>
        <div class="box-body no-padding">
            <div class="box-header with-border">
                <ul class="menu">
                    <li v-for="item in notification" :key="item.id">
                        <router-link :to="item.url" :class="{'n-content': true, 'active': item.status}">
                            <span class="n-date">{{ getTime(item.created_at).fromNow() }}</span> | <span class="n-content">{{ item.content }}</span>
                        </router-link>
                    </li>
                </ul>
                <paginate v-model="page"
                    :page-count="pageCount"
                    :page-range="3"
                    :margin-pages="2"                                           
                    :click-handler="runPage"
                    :prev-text="`<i class='fa fa-angle-left' aria-hidden='true'></i>`"
                    :next-text="`<i class='fa fa-angle-right' aria-hidden='true'></i>`"
                    :container-class="'pagination tizi-paginate'"
                    :page-class="'page-item'">
                </paginate>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";
import moment from "moment";
moment.locale("vi");
import Paginate from "vuejs-paginate";

export default {
    metaInfo(){
		return {
			title: "Thông báo",
			htmlAttrs: {
				lang: 'vi',
				amp: true
			}
		}
	},
    components: {Paginate},
    data() {
        return {
            api: this.$store.state.domainApi,
            notification: [],
            page:1,
            pageSize:20,
            pageCount:5
        }
    },
    methods: {
        getTime(date){
            var time = new moment(date);
            return time;
        },
        getNotification(){
            axios.get(this.api+"/auth/notification",{
                headers:{ 
                    Authorization: `Bearer ${this.$store.state.token}` 
                },
                params:{
                    page: this.page,
                    pageSize: this.pageSize
                }
            })
            .then(response=>{
                this.pageCount = Math.ceil(response.data.count/this.pageSize);
                this.notification = response.data.notification;
            });
        },
        runPage(page){
            this.page=page;
            this.getMessages();
        }
    },
    mounted() {
        this.getNotification();
    }
}
</script>

<style scoped>
.n-date{
    color: red;
}
.n-content{

}
.pagination.tizi-paginate{
    display: flex;
    justify-content: center;
}
</style>